import Mock from 'mockjs'

var arr = Mock.mock({
	"array|1-10":[
		{
	        "userName": '@name', //
	        "age|1-100": 100, //(1-100)
	        "color": "@color", //
	        "date": "@date('yyyy-MM-dd')", //
	        "url": "@url()", //url
	        "content": "@cparagraph()" //
   		}
	]
});

Mock.mock('http://yusihong.com.cn', arr);


